import dayjs from "dayjs";
import { Model } from "./base/Model";
import { formatNumber } from "../utils/utils";
export class LotItem extends Model {
    constructor() {
        super(...arguments);
        this.myLastBid = 0;
    }
    clearBid() {
        this.myLastBid = 0;
    }
    placeBid(price) {
        this.price = price;
        this.history = [...this.history.slice(1), price];
        this.myLastBid = price;
        if (price > (this.minPrice * 10)) {
            this.status = 'closed';
        }
        this.emitChanges('auction:changed', { id: this.id, price });
    }
    get isMyBid() {
        return this.myLastBid === this.price;
    }
    get isParticipate() {
        return this.myLastBid !== 0;
    }
    get statusLabel() {
        switch (this.status) {
            case "active":
                return `  ${dayjs(this.datetime).format('D MMMM [] HH:mm')}`;
            case "closed":
                return ` ${dayjs(this.datetime).format('D MMMM [] HH:mm')}`;
            case "wait":
                return ` ${dayjs(this.datetime).format('D MMMM [] HH:mm')}`;
            default:
                return this.status;
        }
    }
    get timeStatus() {
        if (this.status === 'closed')
            return ' ';
        else
            return dayjs
                .duration(dayjs(this.datetime).valueOf() - Date.now())
                .format('D[] H[] m[ ] s[ ]');
    }
    get auctionStatus() {
        switch (this.status) {
            case 'closed':
                return `  ${formatNumber(this.price)}`;
            case 'wait':
                return '  ';
            case 'active':
                return '  ';
            default:
                return '';
        }
    }
    get nextBid() {
        return Math.floor(this.price * 1.1);
    }
}
